// 详情页
import React, { useState } from 'react'
import { connect } from 'react-redux'
import { shoucang, quxiao } from './actionCreator'

const Details = (props) => {
    // 获取商品信息
    let detailId = props.location.state.id;
    let detail = {}
    console.log(props.shoucang)
    props.datalist.map((val) => {
        if (val.id === detailId) {
            detail = val
            return;
        }
    })
    // 进入详情页星星渲染
    const [iconStatus, setIconStatus] = useState(false)
    const star = () => {
        if (props.shoucang.length !== 0) {
            props.shoucang.map(val => {
                if (val === detailId) {
                    setIconStatus(!iconStatus)
                    return;
                }
            })
        }
    }
    // 收藏/已收藏星星点击事件 
    const shoucangClick = () => {
        if (!props.username) {
            return props.history.push('/login')
        } else {
            setIconStatus(!iconStatus)
            !iconStatus
                ? props.dispatch(shoucang(detailId))
                : props.dispatch(quxiao(detailId))
        }
    }

    return (
        <div onLoad={() => { star() }}>
            {/* 图片、返回 */}
            <img src={detail.img} style={{ width: '100%' }} />
            <button onClick={() => props.history.goBack()}
                style={{ 
                    position: 'absolute', 
                    top: '10px', 
                    left: '10px', 
                    borderRadius: '50%', 
                    border: '0px', 
                    width: '34px', 
                    height: '34px', 
                    background: '#00000066' 
            }}>
                <i className='iconfont icon-zuojiantou' style={{ color: '#fff' }}></i>
            </button>
            {/* 底部按钮 */}
            <div style={{ 
                    position: 'fixed', 
                    bottom: '0px', 
                    width: '100%', 
                    height: '35px', 
                    padding: '10px 0px', 
                    background: '#fff', 
                    zIndex: 111 
            }}>
                <ul style={{ margin: 0, padding: 0 }}>
                    <li style={liStyle}><i style={{ color: '#ff5000', fontSize: '18px' }} className='iconfont icon-dianpu' />店铺</li>
                    <li style={liStyle}><i style={{ fontSize: '18px' }} className='iconfont icon-kefu' />客服</li>
                    <li style={{ ...liStyle, width: '44px', padding: '0' }} onClick={() => { shoucangClick(detailId) }}>
                        <i style={{ fontSize: '18px', color: iconStatus ? '#ff5000' : '' }} className={iconStatus ? 'iconfont icon-shoucang1' : 'iconfont icon-shoucang2'} /><br />
                        {iconStatus ? '已收藏' : '收藏'}
                    </li>
                    <li style={{ ...liStyle, ...liStyle2 }}>加入购物车</li>
                    <li style={{ 
                                ...liStyle, 
                                ...liStyle2, 
                                borderRadius: 0, 
                                borderTopRightRadius: '20px', 
                                borderBottomRightRadius: '20px', 
                                background: '#FE560A' 
                    }}>
                        立即购买
                    </li>
                </ul>
            </div>
        </div>
    )
}

const liStyle = {
    float: 'left',
    listStyle: 'none',
    fontSize: '12px',
    color: '#999',
    width: '24px',
    padding: '0 10px',
    textAlign: 'center',
    lineHeight: '20px'
}
const liStyle2 = {
    fontSize: '14px',
    color: '#fff',
    textAlign: 'center',
    lineHeight: '40px',
    width: '115px',
    height: '40px',
    padding: '0px',
    background: '#ffc500',
    borderTopLeftRadius: '20px',
    borderBottomLeftRadius: '20px',
}
const mapStateToProps = (state) => ({
    username: state.reducer.username,
    datalist: state.reducer.datalist,
    shoucang: state.reducer.shoucang
})
export default connect(mapStateToProps)(Details);