// 底部标签栏 TabBar
import React, { Component } from 'react'
import { TabBar } from 'antd-mobile'
import { withRouter } from 'react-router-dom'
import 'antd-mobile/dist/antd-mobile.css'

class Mytab extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { pathname } = this.props.location;
        return (<div>
            <div style={{ background: 'rgb(244,244,244)' }}>
                <div style={{
                    position: 'fixed',
                    width: '100%',
                    bottom: '-3px'
                }}>
                    <TabBar style={{ color: 'rgb(93,101,107)' }}
                        tintColor='rgb(93,101,107)'
                        hidden={false}>
                        <TabBar.Item
                            title='首页'
                            key='home'
                            icon={
                                <i className='iconfont icon-icon_home'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-home'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selected={pathname === '/'}
                            onPress={() => {
                                return this.props.history.push('/');
                            }}

                            data-seed='logId'
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            title='收藏'
                            key='cart'
                            icon={
                                <i className='iconfont icon-shoucang2'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-shoucang1'
                                    style={{ fontSize: '21px' }}
                                ></i>
                            }
                            selected={pathname === '/shoucang'}
                            onPress={() => {
                                return this.props.history.push('/shoucang');
                            }}
                            data-seed='logId'
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            title='订单列表'
                            key='order'
                            icon={
                                <i className='iconfont icon-dingdanliebiao'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-dingdanliebiao1'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selected={pathname === '/order'}
                            onPress={() => {
                                return this.props.history.push('/order');
                            }}
                            data-seed='logId'
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            title='我的淘宝'
                            key='mytb'
                            icon={
                                <i className='iconfont icon-wode1'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-wode'
                                    style={{ fontSize: '22px' }}
                                ></i>
                            }
                            selected={pathname === '/mytb'}
                            onPress={() => {
                                return this.props.history.push('/mytb');
                            }}
                            data-seed='logId'
                        >
                        </TabBar.Item>
                    </TabBar>
                </div>
            </div>
        </div>)
    }
}

export default withRouter(Mytab);