// reducer
const defaultStore = {
    datalist: [],
    shoucang: [],
    username: ''
}
const reducer = (state = defaultStore, action) => {
    switch(action.type){
        case 'LOGIN':
            return Object.assign({}, state, {
                username: action.username
            })
        case 'DATA':
            return Object.assign({}, state, {
                datalist: action.datalist
            })
        case 'SHOUCANG':
            return Object.assign({}, state, {
                shoucang: [
                    ...state.shoucang,
                    action.scId
                ]
            })
        case 'QUXIAO':
            let sc = state.shoucang;
            let index;
            sc.map((val, i)=>{
                if(val === action.qxId){
                    index = i;
                    return;
                }
            })
            sc.splice(index, 1);
            return Object.assign({}, state, {
                 sc
            })
        default:
            return state
    }
}

export default reducer