import React from 'react';
import {BrowserRouter as Router,Redirect,Route,Switch} from 'react-router-dom'
import {Provider} from 'react-redux'
import store from './taobao2-2/store'
import Mytab from './taobao2-2/Mytab'
import Home from './taobao2-2/Home'
import Shoucang from './taobao2-2/Shoucang'
import Order from './taobao2-2/Order'
import Mytb from './taobao2-2/Mytb'
import Search from './taobao2-2/Search'
import Details from './taobao2-2/Details'
import Login from './taobao2-2/Login'
const App = () => {
	return <Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/' component={Home}></Route>
				<Route path='/shoucang' component={Shoucang}></Route>
				<Route path='/order' component={Order}></Route>
				<Route path='/mytb' component={Mytb}></Route>
				<Route path='/search' component={Search}></Route>
				<Route path='/details' component={Details}></Route>
				<Route path='/login' component={Login}></Route>
				<Route render={()=><Redirect to='/' />} />
			</Switch>
			<Mytab/>
		</Router> 
	</Provider>
}
export default App;









