// 搜索页面
import React, { Component } from 'react'
import { SearchBar } from 'antd-mobile'

class Search extends Component {
  constructor(props) {
    super(props)
  }
  state = {
    value: '',
  };
  onChange = (value) => {
    this.setState({ value });
  };
  clear = () => {
    this.setState({ value: '' });
  };
  render() {
    return (
      <div style={{ width: '100%', height: '100%', background: 'rgb(245,245,249)', position: 'fixed', zIndex: 111 }}>
        <SearchBar
          value={this.state.value}
          placeholder="Search"
          onChange={this.onChange}
          onClear={this.clear}
          showCancelButton
          onCancel={() => { this.props.history.goBack() }}
        />
      </div>
    );
  }
}

export default Search;
