// 收藏列表渲染
import React, { useState } from 'react'
import { connect } from 'react-redux'
import { quxiao } from './actionCreator'

const Soucanglist = (props) => {
    console.log(props.shoucang)
    // 获取收藏列表信息
    let scarr = [];
    props.shoucang.map((val_id, i) => {
        props.datalist.map(val => {
            if (val.id === val_id) {
                scarr[i] = val;
                return;
            }
        })
    })
    const [sclist, setSclist] = useState(scarr);

    // 取消收藏点击事件
    const quxiaosc = (valid) => {
        props.dispatch(quxiao(valid))
        let scarr2 = [];
        props.shoucang.map((val_id, i) => {
            props.datalist.map(val => {
                if (val.id === val_id) {
                    scarr2[i] = val;
                    return;
                }
            })
        })
        setSclist(scarr2);
    }
    return (
        <div style={{ marginBottom: '60px' }}>
            <div style={{ 
                width: '100%', 
                height: '37px', 
                background: 'rgb(255,133,42)', 
                position: 'fixed', 
                top: 0 
            }}>
                <p style={{ width: '60px', margin: '10px auto', color: '#fff' }}>我的收藏</p>
            </div>
            {/* 渲染收藏列表 */}
            <div style={{ marginTop: '50px' }}>
                {
                    sclist.map((val) => {
                        return <div style={{ 
                                        width: '340px', 
                                        height: '140px', 
                                        margin: '10px auto', 
                                        borderRadius: '5px', 
                                        background: '#fff' 
                                }}>
                            <img src={val.img} style={{ width: '140px', height: '140px' }} />
                            <div style={{ float: 'right', width: '185px', height: '140px'}}>
                                <p>{val.id} {val.title}</p>
                                <p style={{ color: '#ff5000' }}>
                                    <span style={{ fontSize: '13px' }}>￥</span>
                                    <span style={{ fontSize: '17px' }}>{val.price}</span>
                                </p>
                                <button style={{ 
                                            width: '80px', 
                                            height: '30px', 
                                            margin: '15px 0 0 90px', 
                                            background: 'rgb(254,109,5)', 
                                            border: 0, 
                                            borderRadius: '5px', 
                                            color: '#fff', 
                                            fontSize: '13px' 
                                        }}
                                    onClick={() => {
                                        quxiaosc(val.id)
                                    }}
                                >取消收藏</button>
                            </div>
                        </div>
                    })
                }
            </div>
        </div>
    )
}
const mapStateToProps = (state) => ({
    shoucang: state.reducer.shoucang,
    datalist: state.reducer.datalist
})
export default connect(mapStateToProps)(Soucanglist)