// 登录页
import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { login } from './actionCreator'

const Login = (props) => {
    const log = () => {
        props.dispatch(login())
    }
    return (
        <div style={{ width: '100%', height: '100%', background: '#fff', position: 'fixed', zIndex: 111 }}>
            <div style={{
                margin: '40px auto',
                width: '80px',
                height: '80px',
                background: "url('https://gw.alicdn.com/tfs/TB1p.BGQXXXXXbFXFXXXXXXXXXX-160-160.png') no-repeat",
                backgroundSize: '80px'
            }}></div>
            <div style={{padding: '0 20px 0 15px'}}>
            <input type='text' placeholder='手机号/邮箱/会员名' style={inp} /><br />
            <input type='password' placeholder='请输入登录密码' style={inp}/><br />
            <button onClick={log} style={btn}>登录</button>
            {
                props.username
                    ? props.history.goBack()
                    : <Redirect to='/login'></Redirect>
            }
            </div>
        </div>
    )
}
const inp = {
    width: '100%',
    height: '30px',
    border: 0,
    padding: 0,
    margin: '20px auto',
    paddingLeft: '5px',
    borderBottom: '2px solid rgb(255,101,31)'
}
const btn = {
    width: '100%',
    height: '45px',
    border: 0,
    borderRadius: '35px',
    background: 'rgb(255,111,0)',
    color: '#fff',
    marginTop: '40px' 
}
const mapStateToProps = (state) => ({
    username: state.reducer.username
})
export default connect(mapStateToProps)(Login)