// actionCreator

//登录 
const login = () => {
    return (dispatch) =>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'post',
            headers: {
                'Content-type': 'application/json'
            }
        })
        .then(res => res.json())
        .then(res => {
            return dispatch(
                {
                    type: 'LOGIN',
                    username: res.data.name
                }
            )
        })
    }
}
// 获取商品列表信息
const datalist = () => {
    return (dispatch) => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, { 
            method: 'post',
            headers: {
                'Content-type': 'application/json'
            }
         })
            .then(res => res.json())
            .then(res => {
                return dispatch(
                    {
                        type: 'DATA',
                        datalist: res.data
                    }
                )
            })
    }
}
// 收藏
const shoucang = (id) => {
    return (dispatch) => {
        dispatch(
            {
                type: 'SHOUCANG',
                scId: id
            }       
        )
    }
}
// 取消收藏
const quxiao = (id) => {
    return (dispatch) => {
        dispatch(
            {
                type: 'QUXIAO',
                qxId: id
            }
        )
    }
}

export {login, datalist, shoucang, quxiao}