// 首页商品渲染
import React from 'react'
import { Grid } from 'antd-mobile';
import { Link } from 'react-router-dom';
import { datalist } from './actionCreator' 
import { connect } from 'react-redux'

const Goods = (props) => {
    (() => {
        props.dispatch(datalist())
    })()
    let datas = props.datalist;
    return (
        <div style={{
            padding: '6px',
            marginBottom: '50px'
        }}>
            <Grid
                itemStyle={{ background: 'rgb(245,245,249)' }}
                hasLine={false}
                square={false}
                columnNum={2}
                data={datas}
                renderItem={(item) => {
                    return <div style={{
                        width: '171px',
                        height: '260px',
                        background: '#fff',
                        borderRadius: '6px',
                        margin: '-9px auto'
                    }}>
                        <Link to={{pathname: '/details', state: {id: item.id}}}>
                            <img src={item.img}
                                style={{
                                    width: '100%',
                                    borderTopLeftRadius: '6px',
                                    borderTopRightRadius: '6px'
                                }}
                            />
                            <div style={{ height: '100px', padding: '6px 0 0 6px', textAlign: 'left' }}>
                                <span style={{
                                    fontSize: '13px',
                                    display: 'inline-block',
                                    width: '146px',
                                    height: '37px',
                                    margin: '5px 0 0 5px',
                                    overflow: 'hidden',
                                    lineHeight: '19px',
                                    color: '#000'
                                }}>{item.title}</span>
                                <p style={{ margin: '10px 0 0 5px' }}>
                                    <span style={{
                                        color: '#ff5500',
                                        fontSize: '12px'
                                    }}>￥</span>
                                    <span style={{
                                        color: '#ff5500',
                                        fontSize: '14px',
                                        margin: '0 4px 0 4px'
                                    }}>{item.price}</span>
                                    <span style={{
                                        color: '#999',
                                        fontSize: '12px'
                                    }}>{item.id}人已购买</span>
                                </p>
                            </div>
                        </Link>
                    </div>
                }}
            />
        </div>
    )
}
const mapStateToProps = (state) => ({
    datalist: state.reducer.datalist
})
export default connect(mapStateToProps)(Goods)
